<template>
  <el-form ref="form" :model="form" label-width="80px">
    <el-form-item label="城市">
      <el-select v-model="form.city" placeholder="选择城市">
        <el-option label="杭州" value="杭州"></el-option>
        <el-option label="上海" value="上海"></el-option>
        <el-option label="北京" value="北京"></el-option>
        <el-option label="广州" value="广州"></el-option>
        <el-option label="成都" value="成都"></el-option>
        <el-option label="深圳" value="深圳"></el-option>
        <el-option label="南京" value="南京"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="小区名称">
      <el-select
        class="middle-select"
        v-model="form.district"
        filterable
        remote
        placeholder="请输入关键词"
        :remote-method="remoteMethod"
        :loading="loading">
        <el-option
          v-for="item in options4"
          :key="item.uid"
          :label="item.name"
          :value="item">
          {{item.name}}
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="房东手机">
      <el-input class="middle-input" v-model="form.userPhone"></el-input>
    </el-form-item>
    <el-form-item label="租住类型">
      <el-radio-group v-model="form.rentType">
        <el-radio label="整租"></el-radio>
        <el-radio label="合租"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item v-if="form.rentType == '合租'" label="房间名称">
      <el-radio-group v-model="form.houseName">
        <el-radio label="主卧"></el-radio>
        <el-radio label="次卧"></el-radio>
        <el-radio label="床位"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="户型">
      <el-select v-model="form.shiCount" class="small-input" placeholder="卧室">
        <el-option label="1室" value="1"></el-option>
        <el-option label="2室" value="2"></el-option>
        <el-option label="3室" value="3"></el-option>
        <el-option label="4室" value="4"></el-option>
        <el-option label="5室" value="5"></el-option>
      </el-select>
      <el-select v-model="form.tingCount" class="small-input" placeholder="客厅">
        <el-option label="0厅" value="0"></el-option>
        <el-option label="1厅" value="1"></el-option>
        <el-option label="2厅" value="2"></el-option>
      </el-select>
      <el-select v-model="form.weiCount" class="small-input" placeholder="卫生间">
        <el-option label="0卫" value="0"></el-option>
        <el-option label="1卫" value="1"></el-option>
        <el-option label="2卫" value="2"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="总面积">
      <el-input class="small-input" v-model="form.totalArea"></el-input>
      <span>平</span>
    </el-form-item>
    <el-form-item label="卧室面积">
      <el-input class="small-input" v-model="form.area"></el-input>
      <span>平</span>
    </el-form-item>
    <el-form-item label="价格">
      <el-input class="small-input" v-model="form.price"></el-input>
      <span>元/月</span>
    </el-form-item>
    <el-form-item label="装修程度">
      <el-radio-group v-model="form.decorate">
        <el-radio label="毛坯"></el-radio>
        <el-radio label="简单装修"></el-radio>
        <el-radio label="精装修"></el-radio>
        <el-radio label="豪华装修"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="房屋类型">
      <el-radio-group v-model="form.houseType">
        <el-radio label="酒店公寓"></el-radio>
        <el-radio label="小区住宅"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="房屋朝向">
      <el-radio-group v-model="form.oriention">
        <el-radio label="南北"></el-radio>
        <el-radio label="东西"></el-radio>
        <el-radio label="南"></el-radio>
        <el-radio label="北"></el-radio>
        <el-radio label="东"></el-radio>
        <el-radio label="西"></el-radio>
      </el-radio-group>
    </el-form-item>
    <el-form-item label="付款方式">
      <el-select v-model="form.payType" placeholder="选择付款方式">
        <el-option label="押一付三" value="押一付三"></el-option>
        <el-option label="押一付二" value="押一付二"></el-option>
        <el-option label="押一付一" value="押一付一"></el-option>
        <el-option label="半年付" value="半年付"></el-option>
        <el-option label="整年付" value="整年付"></el-option>
        <el-option label="押零付一" value="押零付一"></el-option>
      </el-select>
    </el-form-item>

    <el-form-item label="房源照片">
      <input type="file" multiple @change="onFileChange">
    </el-form-item>
  
    <el-form-item>
      <el-button type="primary" @click="onSubmit">发布</el-button>
      <el-button>取消</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
import axios from 'axios';
import jsonp from 'jsonp';

console.log(AV);

const BAIDU_API = 'https://api.map.baidu.com/place/v2/suggestion?city_limit=true&output=json&ak=gEgvK6detS8ubGykV6Nxazpg2UZ75TX6';

const GEOCODER_API = 'https://api.map.baidu.com/geocoder/v2/?callback=renderReverse&output=json&pois=1&ak=gEgvK6detS8ubGykV6Nxazpg2UZ75TX6'

export default {
  data() {
    return {
      form: {
        city: '',
        rentType: '合租',
        houseName: '主卧',
        houseType: '小区住宅',
        payType: '押一付三',
        shiCount: '3',
        tingCount: '1',
        weiCount: '1',
        oriention: '南北',
        decorate: '精装修',
        region: '',
      },
      options4: [],
      loading: false,
      imgList: [],
    };
  },
  methods: {
    onSubmit() {
      const that = this;
      const { district, userPhone, rentType, houseName, shiCount, tingCount, weiCount, totalArea, area, price, decorate, houseType, oriention, payType } = this.form;
      const house = {
        city: district.city,
        cityid: district.cityid,
        district: district.district,
        districtName: district.name,
        location: new AV.GeoPoint(district.location.lat, district.location.lng),
        userId: '5947ebb0fe88c2006a8daa61',
        userPhone,
        rentType: rentType == '合租' ? houseName : '整租',
        houseName: rentType == '合租' ? houseName : '',
        houseInfo: `${shiCount}室${tingCount}厅${weiCount}卫`,
        floor: '未填',
        totalArea,
        price: Number(price),
        bedroom: Number(shiCount),
        decorate,
        houseType,
        oriention,
        payType,
      };
      if (rentType == '合租') {
        house.area = area;
      }

      console.log('--district--', district)
      
      const location = district.location;
      jsonp(`${GEOCODER_API}&location=${location.lat},${location.lng}`, null, (err, data) => {
        const business = data.result.business.split(',');
        house.region = business;

        const images = [];
        Object.keys(that.imgList).forEach(k => {
          images.push(that.imgList[k]);
        });
        console.log('--images--', images);

        images.map(file => () => new AV.File(file.name, file).save()).reduce(
          (m, p) => m.then(v => AV.Promise.all([...v, p()])),
          AV.Promise.resolve([])
        ).then(files => {
          const imgUrls = files.map(file => file.url());
          house.images = imgUrls;
          console.log('--imgurls--', imgUrls, house);


          const H = AV.Object.extend('house');
          const h = new H(house);

          const Counter = new AV.Object('counter');
          Counter.set('totalView', 0);
          h.set('viewTimes', Counter); // 设置这个字段关联counter表
          h.save().then(function (house) {
            console.log('--res--', house)
            that.$alert('发布成功', '提示', {
              confirmButtonText: '确定',
              callback: action => {
                
              }
            });
          });

        });

      });

      // 图片上传不能使用微信小程序的方式，而应该是 new File 的方式
      // 还有设置用户id，用户phone，viewTimes

    },
    remoteMethod(query) {
      const that = this;
      if (query !== '') {
        this.loading = true;
        setTimeout(() => {
          jsonp(`${BAIDU_API}&region=${that.form.city}&query=${query}`, null, (err, data) => {
            this.loading = false;
            this.options4 = data.result;
          })
        }, 10);
      } else {
        this.options4 = [];
      }
    },
    
    onFileChange(e) {
      var files = e.target.files || e.dataTransfer.files;
      if (!files.length)
        return;
      this.imgList = files;
      console.log('--files--', files, Array.from(files), typeof Array.from(files));
    },
  },

};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.small-select {
  width: 100px;
}
.middle-select {
  width: 300px;
}
.small-input {
  width: 100px;
}
.middle-input {
  width: 200px;
}

img {
  width: 30%;
  margin: auto;
  display: block;
  margin-bottom: 10px;
}
</style>



